import React, { useEffect } from "react";
// import { Link } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Auth from "../../auth";

const Home = () => {
  useEffect(() => {
    Auth.checkAuth();
  }, []);
  const links = [
    { name: "Table", url: "/table" },
    { name: "Bets", url: "/user/" + Auth.getUserId() },
  ];
  return (
    <Container className="App bg-champions align-content-center " fluid>
      <img
        src={picture}
        alt="Champions League Logo"
        width={250}
        height={250}
        className="rounded-circle border border-2 border-dark mb-5 fade-in-slow "
      />
      <Row className="gy-3 fade-in-slow ">
        {links.map((item, i) => (
          <Col xs={12} key={i}>
            <Card>
              <Card.Header>
                <Card.Title>
                  <h1
                    className="pointer"
                    onClick={() => (window.location.href = item.url)}
                  >
                    {item.name}
                  </h1>
                </Card.Title>
              </Card.Header>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Home;

const picture =
  "data:image/jpeg;base64,";
