import React, { useEffect } from "react";
import { Navbar, Nav, Button } from "react-bootstrap";
import Auth from "../../auth";
import User from "../../models/User";
const NavbarComponent = () => {
  const distance = " me-3";
  const [isAuthenticated, setIsAuthenticated] = React.useState(false);
  const [user, setUser] = React.useState(null);
  useEffect(() => {
    async function isUserAuth() {
      const isNotAuthenticated = !(await Auth.isAuthenticated());
      setIsAuthenticated(!isNotAuthenticated);

      const u = await User.getUser(Auth.getUserId(), false);
      if (u) {
        setUser(u);
      }
    }

    isUserAuth();
  }, []);
  if (!isAuthenticated) {
    return (
      <Navbar
        className="text-dark bg-champions-only text-white border-bottom"
        expand="lg"
      >
        <Navbar.Brand className={"ms-3  " + distance}>
          <img
            alt="logo"
            width={50}
            height={50}
            className="rounded-circle"
            src={img}
          />
        </Navbar.Brand>
      </Navbar>
    );
  }
  return (
    <Navbar
      className="text-dark bg-champions-only text-white border-bottom"
      expand="lg"
    >
      <Navbar.Brand href="/" className={"ms-3  " + distance}>
        <img
          alt="logo"
          width={50}
          height={50}
          className="rounded-circle"
          src={img}
        />
      </Navbar.Brand>
      <Nav.Link href="/table" className={"opacity-75 " + distance}>
        Table
      </Nav.Link>
      <Nav.Link
        href={"/user/" + Auth.getUserId()}
        className={"opacity-75 " + distance}
      >
        Bets
      </Nav.Link>
      {user?.isAdmin && (
        <Nav.Link href={"/admin"} className={"opacity-75 " + distance}>
          Admin
        </Nav.Link>
      )}
      <Button
        onClick={() => Auth.logout()}
        className={"opacity-75 mx-auto " + distance}
      >
        Logout
      </Button>
    </Navbar>
  );
};

export default NavbarComponent;

const img =
  "data:image/jpeg;base64,";
